@import url(https://fonts.googleapis.com/css?family=Roboto:400,700);
.EraSelectContainer {
  -webkit-perspective: 500px;
          perspective: 500px;
}

.EraSelectContainer > div > div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.EraView__info {
  color: #ffffff;
  position: absolute;
  z-index: 2;
  left: 5%;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 70%;
  -webkit-animation: wiggle2 2s ease-in-out infinite alternate;
          animation: wiggle2 2s ease-in-out infinite alternate;
}

.EraView__title {
  font-size: 100px;
  line-height: 99px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 900;
  text-transform: uppercase;
  /* line-height: 1.5; */
  position: relative;
  padding: 19px 0;
}

.EraView__title::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50%;
  height: 2px;
  background: #ffffff;
}

.EraView__subTitle {
  font-size: 14px;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  margin-top: 12px;
}

.EraView__current {
  font-size: 14px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
}

.EraView__link {
  display: inline-block;
  text-align: center;
  border-radius: 25px;
  color: #ffffff;
  background: #1c1c1c;
  text-decoration: none;
  padding: 1em 2em;
  margin: 31px 0 0 0;
  transition: 0.26s ease;
}

.EraView__link:hover {
  background: #fff;
  color: #000;
}

.App .EraView__bgImageContainer {
  display: flex;
  justify-content: flex-end;
}

.App .EraView__bgImage {
  position: relative;
  background-size: cover;
  -webkit-transform: rotate(-3deg);
          transform: rotate(-3deg);
  -webkit-transform-origin: center;
          transform-origin: center;
  left: 2%;
  width: 50vw;
  height: 80vh;
  -webkit-animation: wiggle 2s ease-in-out infinite alternate;
          animation: wiggle 2s ease-in-out infinite alternate;
}

.App .EraView__bgImageContainer.reverse > .EraView__bgImage {
  -webkit-transform: rotate(3deg);
          transform: rotate(3deg);
}

@media (max-width: 40em) {
  .EraView__title {
    line-height: 1;
    font-size: 32px;
  }
  .EraView__subTitle {
    line-height: 1;
    font-size: 12px;
  }
  .EraView__link {
    margin-left: 50%;
    align-self: flex-end;
  }
  .App .EraView__bgImage {
    height: 50vh;
    width: 90vw;
  }
}

@-webkit-keyframes wiggle {
  0% {
    -webkit-transform: translateY(-1%) rotate(-3deg);
            transform: translateY(-1%) rotate(-3deg);
  }
  100% {
    -webkit-transform: translateY(1%) rotate(-3deg);
            transform: translateY(1%) rotate(-3deg);
  }
}

@keyframes wiggle {
  0% {
    -webkit-transform: translateY(-1%) rotate(-3deg);
            transform: translateY(-1%) rotate(-3deg);
  }
  100% {
    -webkit-transform: translateY(1%) rotate(-3deg);
            transform: translateY(1%) rotate(-3deg);
  }
}

@-webkit-keyframes wiggle2 {
  0% {
    -webkit-transform: translateY(-49%);
            transform: translateY(-49%);
  }
  100% {
    -webkit-transform: translateY(-51%);
            transform: translateY(-51%);
  }
}

@keyframes wiggle2 {
  0% {
    -webkit-transform: translateY(-49%);
            transform: translateY(-49%);
  }
  100% {
    -webkit-transform: translateY(-51%);
            transform: translateY(-51%);
  }
}

.bar {
  background: #ffffff;
  bottom: 7px;
  height: 3px;
  position: absolute;
  width: 5px;
  border-radius: 1px;
  opacity: 0.3;
}

.animated_bar {
  -webkit-animation: sound 0ms -800ms linear infinite alternate;
          animation: sound 0ms -800ms linear infinite alternate;
}

.bar:nth-child(1) {
  left: 7px;
  -webkit-animation-duration: 474ms;
          animation-duration: 474ms;
}

.bar:nth-child(2) {
  left: 13px;
  -webkit-animation-duration: 433ms;
          animation-duration: 433ms;
}

.bar:nth-child(3) {
  left: 19px;
  -webkit-animation-duration: 407ms;
          animation-duration: 407ms;
}

canvas {
  position: fixed;
  top: 0;
  left: 0;
  border: 0;
  z-index: 999;
  pointer-events: none;
  mix-blend-mode: color-dodge;
  opacity: 0.7;
}

.logo-loader {
  -webkit-animation: bounce 1s ease infinite alternate;
          animation: bounce 1s ease infinite alternate;
}

@-webkit-keyframes sound {
  0% {
    opacity: 0.35;
    height: 3px;
  }
  100% {
    opacity: 1;
    height: 16px;
  }
}

@keyframes sound {
  0% {
    opacity: 0.35;
    height: 3px;
  }
  100% {
    opacity: 1;
    height: 16px;
  }
}

@-webkit-keyframes bounce {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  30% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  50% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
  70% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  80% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes bounce {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  30% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  50% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
  70% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  80% {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}


/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}
body {
  line-height: 1.5;
  font-size: 14px;
  font-family: 'Roboto', sans-serif;
}

b {
  font-weight: bold;
}
